import {Component, useContext} from "react";
import {AuthConsumer, AuthContext} from "./auth";
import { motion } from "framer-motion"

const Home = (props) => {
	const authContext = useContext(AuthContext)
	console.log("[Home] authContext:", authContext)
	return (
			<AuthConsumer>
				{ ({username}) => {return (
						<div
								// inital={{ opacity: 0}}
								// animate={{ opacity: 1 }}
								// exit={{ opacity: 0 }}
						>

							<h1>Username: {username}</h1>
							<button onClick={() => props.history.push("/dashboard")}>
								Dashboard
							</button>
							<button onClick={() => props.history.push("/about")}>
								About
							</button>
							<button onClick={() => props.history.push("/login")}>
								Login
							</button>
							<button onClick={() => authContext.logout()}>
								Sign out
							</button>
						</div>
				)}
				}
			</AuthConsumer>
	)
}

export default Home