import React, {useContext} from "react"
import {Route, useHistory} from "react-router";
import {AuthContext, AuthEnum } from "./auth";

const ProtectedRoute = ({component: Component, ...rest }) => {
	// const [isAuthenticated, setAuthenticated] = useState(false)
	// const [isPending, setPending] = useState(true)
	const history = useHistory()
	const authContext = useContext(AuthContext)

	console.log()
	console.log("[ProtectedRoute]")
	console.log("[ProtectedRoute] username:", authContext.username)
	console.log("[ProtectedRoute] isAuthenticated:", authContext.isAuthenticated)
	console.log()
	console.log("Rest")
	console.log(rest)
	console.log()

	if ( authContext.isAuthenticated === AuthEnum.PENDING )
		return (
				<>
					<div className="loader"/>
					<h1>Attempting to authenticate session</h1>
				</>
		)
	else if (authContext.isAuthenticated === AuthEnum.UNAUTHENTICATED)
		return (
				<>
					<h1>User is not authenticated</h1>
					<button onClick={() => history.push("/login")}>Login</button>
				</>
		)

	return (
			<Route {...rest}
			       render={props => {
				       return <Component {...props} />
			       }}
			/>
	)


	// console.log("debug isAuthenticated: ", isAuthenticated)
	// console.log("debug isPending: ", isPending)
	// // console.log("debug props to pass on: ", [...rest])
	// console.log("rest: ", rest)
	//
	// if (isPending) {
	// 	console.log("Rendering pending msg")
	// 	return <h1>Pending ... </h1>
	// }
	//
	// return (
	// 		<Route
	// 				{...rest}
	// 				render={props => {
	// 					if (isAuthenticated) {
	// 						return <Component {...props} />
	// 					} else {
	// 						return (
	// 								<Redirect
	// 										to={{
	// 											pathname: "/notAuthenticated",
	// 											state: {
	// 												from: props.location
	// 											}
	// 										}}
	// 								/>
	// 						)
	// 					}
	// 				}}
	// 		/>
	// )
}


export default ProtectedRoute
